import { getMouseEventOptions } from "@testing-library/user-event/dist/utils";
import { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import MovieDetail from "../components/MovieDetail";

function Detail() {
  const { id } = useParams();
  const [loading, setLoading] = useState(true);
  const [movie, setMovie] = useState([]);
  const getMovie = async () => {
    const json = await (
      await fetch(`https://yts.mx/api/v2/movie_details.json?movie_id=${id}`)
    ).json();
    setMovie(json.data.movie);
    setLoading(false);
  };
  useEffect(() => {
    getMovie();
  }, [getMovie]);
  return (
    <div>
      {loading ? (
        <h1>loading . . .</h1>
      ) : (
        <div>
          <MovieDetail
            medium_cover_image={movie.data.movie.medium_cover_image}
            title={movie.data.movie.title}
            description_full={movie.data.movie.description_full}
            year={movie.data.movie.year}
            genres={movie.data.movie.genres}
            rating={movie.data.movie.rating}
            runtime={movie.data.movie.runtime}
          />
        </div>
      )}
    </div>
  );
}

export default Detail;
